//
import axios from "axios";
import showMessage from "../../utils/showMessage.js";
const ins = axios.create({}) //axiosaxios
ins.interceptors.response.use(function (resp) { //
    // console.log("")
    if (resp.data.code === 0) {
        return resp.data.data
    }
    // console.log(resp.data.data)
    showMessage({
        content: "",
        type: "error"
    })
    return null
})
export default ins