<template>
    <!-- 1 -->
    <div class="pager-container" v-if="pageNumber >= 1">
      <!-- class,disabled1 -->
      <a @click="handleClick(1)" :class="{ disabled: current === 1 }"
        >|&lt;&lt;</a
      >
      <a @click="handleClick(current - 1)" :class="{ disabled: current === 1 }"
        >&lt;&lt;</a
      >
      <a
        @click="handleClick(n)"
        v-for="(n, i) in numbers"
        :class="{ active: current === n }"
        :key="i"
        >{{ n }}</a
      >
  
      <!-- disabled -->
      <a
        @click="handleClick(current + 1)"
        :class="{ disabled: current === pageNumber }"
        >&gt;&gt;</a
      >
      <a
        @click="handleClick(pageNumber)"
        :class="{ disabled: current === pageNumber }"
        >&gt;&gt;|</a
      >
    </div>
  </template>
  
  <script>
  export default {
    props: {
      current: {
        type: Number,
        default: 1,
      },
      total: {
        type: Number,
        default: 10,
      },
      limit: {
        type: Number,
        default: 10,
      },
      visibleNumber: {
        type: Number,
        default: 10,
      },
    },
    computed: {
      //
      pageNumber() {
        return Math.ceil(this.total / this.limit);
      },
      //
      minPage() {
        let min = this.current - Math.ceil(this.visibleNumber / 2);
        //1
        if (min < 1) {
          min = 1;
        }
        return min;
      },
      maxPage() {
        let max = this.minPage + this.visibleNumber - 1;
        // 
        if (max > this.pageNumber) {
          max = this.pageNumber;
        }
        return max;
      },
      numbers() {
        let arr = [];
        for (let i = this.minPage; i < this.maxPage; i++) {
          arr.push(i);
        }
        return arr;
      },
    },
    methods: {
      handleClick(newPage) {
        //
        if (newPage < 1) {
          newPage = 1;
        }
        if (newPage > this.pageNumber) {
          newPage = this.pageNumber;
        }
        if ((newPage === this.current)) {
          return;
        }
        // console.log(newPage)
        //
        this.$emit("pageChange", newPage);
      },
    },
  };
  </script>
  
  <style lang="less" scoped>
  @import "~@/styles/var.less"; //~@src
  .pager-container {
    display: flex;
    justify-content: center;
    margin: 20px 0;
    a {
      cursor: pointer;
      color: @primary;
      margin: 0 6px;
      &.disabled {
        color: @lightWords;
        cursor: not-allowed;
      }
      &.active {
        color: @words;
        font-weight: bold;
        cursor: text;
      }
    }
  }
  </style>>
  