<template>
  <!-- style -->
  <img
    :src="url"
    :style="{
      width: size + `px`,
      height: size + `px`,
    }"
    class="avatar-img"
  />
</template>

<script>
export default {
  props: {
    url: {
      type: String, //url
      required: true, //
    },
    size: {
      type: Number,
      default: 150, //
    },
  }, //
};
</script>

<style>
.avatar-img {
  border-radius: 50%;
  object-fit: cover;
  display: block;
}
</style>