<template>
  <div class="blog-toc-container">
    <h2></h2>
    <BlogTOC :list="TOCwithSelect" @selected="handleSelect" />
  </div>
</template>

<script>
import BlogTOC from "./RightList.vue";
import { debounce } from "../../../../utils/index.js";
export default {
  props: {
    cateList: {
      type: Array,
      // required: true,
    },
  },
  components: {
    BlogTOC,
  },
  data() {
    return {
      //hash
      activeAnchor: "",
    };
  },
  methods: {
    handleSelect(e) {
      location.hash = e.anchor;
    },
    //activeAnchor
    setActiveAnchor(scrollDom) {
      if (!scrollDom) {
        return;
      }
      const range = 200;
      this.activeAnchor = ""; // 
      for (const dom of this.doms) {
        if (!dom) {
          continue;
        }
        //
        const top = dom.getBoundingClientRect().top;
        //>0 <=200
        if (top > 0 && top <= range) {
          this.activeAnchor = dom.id; //activeAnchor
          return;
        }
        //
        else if (top > range) {
          return;
        } else {
          this.activeAnchor = dom.id;
        }
      }
    },
  },
  created() {
    //
    this.setSelectDebounce = debounce(this.setActiveAnchor, 50);
    this.$bus.$on("mainScroll", this.setSelectDebounce);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.setSelectDebounce);
  },

  computed: {
    //TOCisSelectedlist
    TOCwithSelect() {
      const getSelectTOC = (toc = []) => {
        return toc.map((it) => ({
          ...it,
          isSelected: it.anchor === this.activeAnchor,
          children: getSelectTOC(it.children),
        }));
      };
      return getSelectTOC(this.cateList);
    },
    //tocdom
    doms() {
      const doms = [];
      const addToDoms = (toc) => {
        for (const dom of toc) {
          doms.push(document.getElementById(dom.anchor));
          if (dom.children && dom.children.length > 0) {
            addToDoms(dom.children);
          }
        }
      };
      addToDoms(this.TOCwithSelect);
      return doms;
    },
  },
};
</script>

<style lang="less" scoped>
h2 {
  font-weight: bold;
  letter-spacing: 2px;
  font-size: 1em;
  margin: 0;
}
</style> 